
body{
  background: #020024;
  background: linear-gradient(275deg,rgba(2, 0, 36, 1) 0%, rgba(18, 33, 54, 1) 35%, rgba(3, 3, 31, 1) 100%);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
}


.App{
  width: auto;
  min-height: 100vh;
  /*background-color: #2A2927;*/
  background-color: transparent;
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: center;
}

.LeftWindow, .RightWindow{
  width: 436px;
  min-height: 100vh;
  background: #0C182B;
  background: linear-gradient(319deg,rgba(12, 24, 43, 1) 21%, rgba(32, 59, 96, 1) 78%);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0 15px;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 992px){

  #root{
    width: 100%;
  }

  .LeftWindow, .RightWindow{
    max-width: 900px;
  }

  .LeftWindow{
    display: block;
    opacity: 1;

    transition: 0.3s ease-in-out;
  }

  .RightWindow{
    display: block;
    opacity: 1;

    transition: 0.3s ease-in-out;
  }

  .LeftWindowInactive{
    display: none;
    opacity: 0.2;
    margin-left: -800px;
    transition: 0.3s ease-in-out;
  }

  .RightWindowInactive{
    display: none;
    opacity: 0.2;
    margin-left: 800px;
    transition: 0.3s ease-in-out;
  }

}

@media screen and (max-width: 576px){
  .LeftWindow, .RightWindow{
    max-width: 375px;
  }

}
